                  <!-- <template v-slot:item="data">
                      <v-list-item-content active-class="green--text" :disabled="true" >
                        <v-list-item-title @click="saludar()" class="red--text text-lighten-4" v-html="data.item.muestra"></v-list-item-title>
                      </v-list-item-content> 
                  </template> -->
                </v-autocomplete>
            <v-btn :disabled="idMuestraImagenSeleccionado?false:true" icon color="white" class="red darken-4 flex-row mx-2" @click="agregarExamenImagen(idMuestraImagenSeleccionado)" ><v-icon>add</v-icon></v-btn>
  
         </div>   
        </v-col>
      
   </v-row>
 <h5>Examenes Realizados</h5>
  <p class="font-weight-bold red--text">{{mensajeExamen}} </p>
   {{examenesImg}}
  <v-row class="ma-0 pa-0" >              
              <v-col cols="12" sm="6" md="6 mb-2" class="ma-0 pa-0" v-for="(item,index) in examenesImg" :key ="index" >
                <v-card  outlined  class="pt-3 mx-1 px-1 red lighten-3" >
                  <v-btn @click="eliminarExamen(examenesImg[index])" class="float-right red" :x-small="true" color="white"  icon> <v-icon>close</v-icon></v-btn>
                  <h3 class="text-center">{{examenesImg[index].descripcionTipoMuestraCard}} ({{examenesImg[index].descripcionMuestraCard}})</h3>             
             <v-row  class="ma-0 pa-0" >
                      <v-col  cols="10" lg="11" class="ma-0 pa-0">
                         <v-text-field :rules="requeridoRules" v-model="examenesImg[index].nroVoucher"  color="red darken-4" label="Boleta o Factura" dense outlined background-color="red lighten-4" id="nroVoucherImg"></v-text-field>          
                      </v-col>
                      <v-col class="ma-0 pa-0" cols="1" lg="1">
                        <v-btn   @click="copiarTexto()"   block dark color="red darken-4" > <v-icon>content_copy</v-icon></v-btn>
                      </v-col>
               </v-row>  
             
                    
                  <v-row  justify="center" class="ma-0 pa-0">
                    
                    <v-col  cols="12" sm="12" md="6" class="pa-0"> 
                       
                          <v-select :items="itemsTipoAtencion" 
                          :rules="requeridoRules"
                            item-color="red darken-4" 
                            class=" ma-0 pa-0 red--text text-lighten-4" 
                            label="Tipo de Atencion" 
                            color="red darken-4" 
                            background-color="red lighten-4"  
                            item-text="descripcion"
                            item-value="idTipoAtencion"
                            v-model="examenesImg[index].idTipoAtencion"
                            dense  
                            outlined>
                          </v-select>
                       
                          <v-select :items="itemsTipoPlaca" 
                          :rules="requeridoRules"
                          item-color="red darken-4" 
                          class=" ma-0 pa-0 red--text text-lighten-4" 
                          label="Tipo de Placa" 
                          color="red darken-4" 
                          background-color="red lighten-4"  
                          item-text="descripcion"
