                          </v-select>

                         <v-select 
                         :items="itemsEspecialidad" 
                         :rules="requeridoRules"
                          item-color="red darken-4" 
                          class="red--text text-lighten-4 ma-0 pa-0" 
                          label="Especialidad" 
                          color="red darken-4" 
                          background-color="red lighten-4"  
                          item-text="descripcion"
                          item-value="idEspecialidad"
                          v-model="examenesImg[index].idEspecialidad"
                          dense 
                          outlined>
                          </v-select>  
                          <v-select :items="itemsMedico" 
                          :rules="requeridoRules"
                          item-color="red darken-4" 
                          class="red--text text-lighten-4" 
                          label="Med.Informante" 
                          color="red darken-4" 
