                          </v-select>
                     </v-col>
                      <v-col class="ma-0 pa-0" cols="12" sm="12" md="6">
                         <v-select :items="itemsMedico" 
                         :rules="requeridoRules"
                          item-color="red darken-4" 
                          class="red--text text-lighten-4" 
                          label="Med.Seguimiento" 
                          color="red darken-4" 
                          background-color="red lighten-4"  
                          item-text="descripcion"
                          item-value="idMedico"
                          v-model="examenesImg[index].detalleRolMedico[0].idMedico"
                          dense 
                          outlined>
                          </v-select>
                        <v-row  justify="center" class="ma-0 pa-0">
                          <v-col  cols="5" class="ma-0 pa-0">
                            <v-text-field class="ma-0 pa-0" v-model="examenesImg[index].nroFallas" type="number"  color="red darken-4" label="Fallas" dense outlined background-color="red lighten-4"  ></v-text-field>
                           </v-col>
                          <v-col  cols="7" class="ma-0 pa-0">
                            <v-text-field class="ma-0 pa-0" v-model="examenesImg[index].importe" type="number"  color="red darken-4" label="Importe" dense outlined background-color="red lighten-4"  ></v-text-field>
                           </v-col>
                        </v-row>
                      <v-text-field  v-model="examenesImg[index].fechaAtencion" type="date"  color="red darken-4" label="Fecha Atencion" dense  outlined background-color="red lighten-4"  ></v-text-field>
                      <v-text-field v-model="examenesImg[index].fechaResultado" type="date"  color="red darken-4" label="Entrega del Resultado" dense  outlined background-color="red lighten-4"  ></v-text-field>
                    </v-col>            
                  </v-row>                   
                </v-card>
              </v-col>
      </v-row>

  <v-row v-if="!ambienteModificacion" justify="center" class="ma-0 pa-0">
    <v-col cols="6">
     <v-btn v-if="examenesImg.length>0" class="px-5" block dark color="red darken-4" type="submit">Guardar</v-btn>
    </v-col>
  </v-row>
  <v-row v-else>
    <v-col cols="6">
          <v-btn class="px-5" block dark color="red darken-4" @click="modificar()">Modificar</v-btn>
    </v-col >
    <v-col  cols="6">
          <v-btn class="px-5" block dark color="red darken-4" @click="cancelar()">Cancelar</v-btn>
    </v-col>
  </v-row>
    <v-row justify="center" class="ma-0 pa-0">
    <v-alert v-if="mensajeError!==''" dense outlined type="error">
          {{ mensajeError }}</v-alert>
     <v-alert v-if="mensajeExitoso" dense class="mt-4 text-center" type="success">
                                {{mensajeExitoso}}    </v-alert>
  </v-row>
  </v-form>
  <v-row>
     <v-col cols="12"  class="mx-auto">
      <Tabla  @moverPosicion="moverHaciaFormulario"  @dataModificarImagen="obtenerDataParaModificacion"  ref="tabla"></Tabla >
      </v-col>
     
  </v-row>
 </v-container>
</template>
<script>
import * as easings from 'vuetify/lib/services/goto/easing-patterns'
import Tabla from '../components/TablaImagenes.vue'
import {mapState,mapActions} from 'vuex'
export default {
  name:'ModuloImagenes',
  components:{Tabla},
  data() {
    return {  
      //parte del scroll
        type: 'number',
        number: 1,
        duration: 1000,
        offset: 0,
        easing: 'easeInOutCubic',
        easings: Object.keys(easings),
      //termina parte del scroll
       btnObtenerPaciente:false,
        dniRules: [(v) => (!!v && (v.length==8) )|| "8 Numeros"],
        requeridoRules: [(v) => !!v || "es requerido"],
        examenesImg: [],
        itemsEspecialidad:[],
        itemsMedico: [],
        itemsTipoMuestraImagen:[],
        itemsMuestraImagen:[],
        itemsTipoPlaca:[],
        itemsTipoAtencion :[],
        itemsTempEliminados:[],
        idTipoMuestraImagenSeleccionado:0,
        idMuestraImagenSeleccionado:0,
        TipoMuestraImagen:'',
        dni:'',
        nombres:'',
        apellidos:'',
        fechaNacimiento:'',
        idPaciente:0,
        ambienteModificacion:false,
        mensajeError:'',
        mensajeExitoso:''
     }
  },
   mounted(){
    this.listarTipoAtencion();
    this.listarMedico();
    this.listarTipoPlaca();
    this.listarEspecialidad();
    this.listartTipoMuestraImagen();
  },
  computed:{
    //parte del scroll
    target () {
        const value = this[this.type]
        if (!isNaN(value)) return Number(value)
        else return value
      },
      options () {
        return {
          duration: this.duration,
          offset: this.offset,
          easing: this.easing,
        }
      },
    //scroll termina
     mensajeExamen:function(){
        if(!this.examenesImg.length>0)
            return 'Se require seleccionar Examenes'
            return ''
        },
    ...mapState(['token']),
   
  },
  watch:{
     idTipoMuestraImagenSeleccionado:function(val){
              if(val>0) this.listarMuestraImagen(val)
          }
  },
  methods: {
    moverHaciaFormulario() {
                this.$vuetify.goTo(this.target, this.options)
           },
 ...mapActions(['getPaciente']),
        async  obtenerPacienteImagenes(){
           this.btnObtenerPaciente= await true;
           this.nombres='';
            this.apellidos='';  
          const api =localStorage.getItem('api');
         var dataPaciente= await this.getPaciente({payload:this.dni});

          if(dataPaciente.nombres){
            this.nombres=dataPaciente.nombres;
            this.apellidos=dataPaciente.apellidos;
          }else{
                  if(dataPaciente=="Ha superado la cantidad de consultas mensuales: 250" && api!=2){
                        localStorage.setItem('api',2);
                        this.obtenerPacienteImagenes();
                      }else{
                          this.mensajeNoData=dataPaciente;
                          this.dialogNoData=true;
                        }
              }
            this.btnObtenerPaciente=await false;
          },

    obtenerFecha(i){
          const hoy = new Date();
          var aÃ±o=hoy.getFullYear()
          var mes=(hoy.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=hoy.getDate()
              dia=(dia<10)?('0'+dia):dia;
            if(i==0){ return aÃ±o+'-'+mes+'-'+dia;}
            else{
                  if(hoy.getDay()==6){
                     dia =Number(dia)+2
                     dia=dia<10?('0'+dia):dia;
                     return aÃ±o+'-'+mes+'-'+dia;
                  }
                  else{
                     dia =(Number(dia)+1);
                     dia=dia<10?('0'+dia):dia;
                     return aÃ±o+'-'+mes+'-'+dia;
                  }
              }
      },
    agregarExamenImagen(idMuestra){
       var desc='';
       var descMuestra='';
       var existe=false;
       var registroImagenes = this.$refs.formularioImagenes.validate();
      if(idMuestra  && registroImagenes){
        //comprobando si existe el examen
          this.examenesImg.filter(e=> {if(e.id==idMuestra){existe=true}})
         if(!existe){
              this.itemsMuestraImagen.filter(e=>{
                if(e.idMuestraImagen==idMuestra) desc=e.descripcion
                return
              })
              this.itemsTipoMuestraImagen.filter(e=>{
                if(e.idTipoMuestraImagen==this.idTipoMuestraImagenSeleccionado) descMuestra=e.descripcion
                return
              })
              if(this.ambienteModificacion){
                  this.examenesImg.push({
                                              id:idMuestra,
                                              opcion:'Nuevo',
                                              descripcionMuestraCard:desc,
                                              descripcionTipoMuestraCard:descMuestra,
                                              fechaRegistroExamen:this.obtenerFecha(0),
                                              fechaAtencion:this.obtenerFecha(0),
                                              fechaResultado:this.obtenerFecha(1),
                                              nroVoucher:'',
                                              importe:0,
                                              idEspecialidad:0,
                                              idTipoAtencion:0,
                                              idMuestraImagen:this.idMuestraImagenSeleccionado,
                                              idTipoPlaca:0,
                                              nroFallas:0,
                                              detalleRolMedico:[{
                                                idMedico:0,
                                                idRolMedico:1 //ID  SEGUIMIENTO
                                                },{
                                                idMedico:0,
                                                idRolMedico:2 //ID INFORMANTE
                                                }]
                                            })
              }else{

                this.examenesImg.push({
                            id:idMuestra,
                            descripcionMuestraCard:desc,
                            descripcionTipoMuestraCard:descMuestra,
                            fechaRegistroExamen:this.obtenerFecha(0),
                            fechaAtencion:this.obtenerFecha(0),
                            fechaResultado:this.obtenerFecha(1),
                            nroVoucher:'',
                            importe:0,
                            idEspecialidad:0,
                            idTipoAtencion:0,
                            idMuestraImagen:this.idMuestraImagenSeleccionado,
                            idTipoPlaca:0,
                            nroFallas:0,
                            detalleRolMedico:[{
                              idMedico:0,
                              idRolMedico:1 //ID  SEGUIMIENTO
                              },{
                              idMedico:0,
                              idRolMedico:2 //ID INFORMANTE
                              }]
                          })
                          
              }
                  this.idMuestraImagenSeleccionado='';
                  desc='';
                  descMuestra='';
                }else{
                    this.idMuestraImagenSeleccionado='';
                } 
         }
    },
      
    eliminarExamen(i){
        this.examenesImg=this.examenesImg.filter(e=>{ if(e.id!=i.id) return e})
      /*si quiere eliminar no tiene que ser uno nuevo pero debe estar 
      en ambiente de modificacion para agregar la opcion eliminar*/

      if(this.ambienteModificacion && i.opcion!=='Nuevo' ){ 
        i.opcion='Eliminar';
        this.itemsTempEliminados.push(i);
      }
     },
    obtenerDataParaModificacion(e){
      //aseegurar la limpieza de los textfieldds
            this.dni='';
            this.nombres='';
            this.apellidos='';
            this.fechaNacimiento=null;
            this.idTipoMuestraImagenSeleccionado=0;
            this.examenesImg=[];
      this.ambienteModificacion=true;
        this.idPaciente=e.idPaciente;
        this.dni=e.dni;
        this.nombres=e.nombres;
        this.apellidos=e.apellidos;
        this.fechaNacimiento=this.$refs.tabla.obtenerFecha(e.fechaNacimiento); 

            this.examenesImg.push({
                             id:e.idMuestra,
                            opcion:'Modificar',
                            idExamen:e.id,
                            descripcionMuestraCard:e.examen,
                            fechaRegistroExamen:this.$refs.tabla.obtenerFecha(e.fechaRegistroExamen),
                            fechaAtencion: e.fechaAtencion.split('/').reverse().join('-'),
                            fechaResultado:this.$refs.tabla.obtenerFecha(e.fechaEntregaResultado),
                            nroVoucher:e.nroVoucher,
                            importe:e.importe,
                            idEspecialidad:this.itemsEspecialidad.filter(ele=>{ if (ele.descripcion==e.especialidad) return ele })[0]?.idEspecialidad,
                            idTipoAtencion:this.itemsTipoAtencion.filter(ele=>{ if (ele.descripcion==e.atencion) return ele})[0]?.idTipoAtencion,
                            idMuestraImagen:e.idMuestra,
                            idTipoPlaca:this.itemsTipoPlaca.filter(ele=>{ if (ele.descripcion==e.placa) return ele})[0]?.idTipoPlaca,
                            nroFallas:e.nroFallas,
                            detalleRolMedico:[{
                              idMedico:this.itemsMedico.filter(ele=>{ if (ele.descripcion==e.seguimiento) return ele})[0]?.idMedico,
                              idRolMedico:1 //ID  SEGUIMIENTO
                              },{
                              idMedico:this.itemsMedico.filter(ele=>{ if (ele.descripcion==e.informante) return ele})[0]?.idMedico,
                              idRolMedico:2 //ID INFORMANTE
                              }]
                          })
    },
    obtenerDescripcionMuestra(id){
        this.itemsMuestraImagen.filter(e=> {return  e.idMuestraImagen==id? e.descripcion:''})
    },
    Guardar(){
     var headers={
        "Content-Type":"application/json",
        token:this.token
        }

    if( this.$refs.formularioImagenes.validate() ){
          this.axios.post('/guardarExamImagenes',{
            dni:this.dni,
            nombres:this.nombres,
            apellidos:this.apellidos,
            fechaNacimiento:this.fechaNacimiento,
            telefono:'',
            empresa:'PAMS',
            examenes:this.examenesImg
          },{headers})
          .then(res=>{  
            this.dni='';
            this.nombres='';
            this.apellidos='';
            this.fechaNacimiento=null;
            this.idTipoMuestraImagenSeleccionado=0;
            this.examenesImg=[]
            this.mensajeExitoso=res.data.mensaje;
            setTimeout(()=>{ this.mensajeExitoso='';},2000);
            this.$refs.tabla.listarExamenesImagenes();
          })
          .catch(err=>{
             err.response.data.sqlMessage?this.mensajeError=err.response.data.sqlMessage :   this.mensajeError=err;            
                setTimeout(()=>{this.mensajeError=''},2000 )
          })
      }
    },

    modificar(){
    //uso nuevo array porque el otro esta relacionado con los cards
      let eData=[];
      var headers={
        "Content-Type":"application/json",
        token:this.token
        }
        console.log('hola');
         if(this.itemsTempEliminados.length>0){
             eData=[...this.examenesImg,...this.itemsTempEliminados]}
          else{
            eData= this.examenesImg
             }
      console.log('hola');
       if( this.$refs.formularioImagenes.validate() && this.examenesImg.length>0){
           
          this.axios.post('/modificarExamImagenes',{
            idPaciente:this.idPaciente,
            dni:this.dni,
            nombres:this.nombres,
            apellidos:this.apellidos,
            fechaNacimiento:this.fechaNacimiento,
            telefono:'',
            empresa:'PAMS',
            examenes:eData
          },{headers})
          .then(res=>{

            this.dni='';
            this.nombres='';
            this.apellidos='';
            this.fechaNacimiento=null;
            this.idTipoMuestraImagenSeleccionado=0;
            this.examenesImg=[]
            this.mensajeExitoso=res.data.mensaje
            this.ambienteModificacion=false;
            setTimeout(()=>{ this.mensajeExitoso='';},2000);
            this.$refs.tabla.listarExamenesImagenes();
            
          })
          .catch(err=>{
             err.response.data.sqlMessage?this.mensajeError=err.response.data.sqlMessage :   this.mensajeError=err;            
                setTimeout(()=>{this.mensajeError=''},2000 )
          })
      }
    },
    cancelar(){
      this.ambienteModificacion=false;
      this.examenesImg=[];
      this.dni='';
      this.nombres='';
      this.apellidos='';
      this.fechaNacimiento=null;
    },
    listartTipoMuestraImagen(){
          this.axios.get('/listarTipoMuestraImagen').then(res=>{ this.itemsTipoMuestraImagen=res.data; })
          .catch(err=>{ console.log(err) })
    },
     listarMuestraImagen(id){
           this.axios.post('/listarMuestraImagen',{idTipoMuestraImagen:id}).
          then((res)=>{
          this.itemsMuestraImagen= res.data})
        .catch(err=>{console.log(err)})
      },
    listarMedico(){
        this.axios.get('/listarMedico').then(res=>{ this.itemsMedico=res.data;
        }).catch(err=>{ console.log(err) })
      },
    listarTipoAtencion(){
        this.axios.get('/listarTipoAtencion').then(res=>{this.itemsTipoAtencion=res.data;
        }) .catch(err=>{ console.log(err)  })
    },
    listarTipoPlaca(){
        this.axios.get('/listarTipoPlaca').then(res=>{this.itemsTipoPlaca=res.data;
        }).catch(err=>{console.log(err)})
    },
    listarEspecialidad(){
        this.axios.get('/listarEspecialidad').then(res=>{this.itemsEspecialidad=res.data;
        }).catch(err=>{console.log(err)})
    },
    copiarTexto(){
       let input=document.getElementById("nroVoucherImg");
       /*obtiene el elemento que esta seleccionado*/
       const selected =  document.getSelection().rangeCount > 0  ? document.getSelection().getRangeAt(0) : false;  
          input.select();
         document.execCommand("copy"); 
          /*Si estas seleccionado entonces no sombrearÃ¡*/
          if(selected){
          document.getSelection().removeAllRanges();    
          document.getSelection().addRange(selected);
          }
    }
  },
}
</script>
