<template>
<div>
  <v-row class="ma-0">
    <v-col cols="12" md="5" class=" pa-1">
       <v-text-field type="date" v-model="desde" color="red darken-4" label="Desde" dense outlined background-color="red lighten-4" ></v-text-field>
    </v-col>
    <v-col cols=10 md="5" class=" pa-1">
       <v-text-field type="date" v-model="hasta" color="red darken-4" label="Hasta" dense outlined background-color="red lighten-4" ></v-text-field>
    </v-col>
    <v-col cols="2" md="2" class="ma-0  pa-1">
       <v-btn  class="py-1" block dark color="red darken-4" @click="listarExamenesImagenes">
                <v-icon>
                  search
                </v-icon>
             </v-btn>
      </v-col>
  </v-row>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Buscar"
              single-line
              hide-details
              class="mb-2"
            ></v-text-field>

          <v-data-table  
            dense
            fixed-header
            :dark="true"      
            :headers="dataHeaders"
            :items="dataBody"
            :search="search"
            class="elevation-1"
            :loading="loading"
            loading-text="Cargando.. Espere porfavor"
          >
          <template v-slot:[`header.id`]="{ header }">
             <th  class="red darken-4 text-center subtitle-1 font-weight-bold white--text " >
                        {{header.text.toUpperCase()}}
             </th>
          </template>
          <template v-slot:[`header.dni`]="{ header }">
             <th  class="red darken-4 text-center subtitle-1 font-weight-bold white--text " >
                        {{header.text.toUpperCase()}}
             </th>
          </template>
          <template v-slot:[`header.paciente`]="{ header }">
             <th  class="red darken-4 text-center subtitle-1 font-weight-bold white--text " >
                        {{header.text.toUpperCase()}}
             </th>
          </template>
           <template v-slot:[`header.importe`]="{ header }">
             <th  class="red darken-4 text-center subtitle-1 font-weight-bold white--text " >
                        {{header.text.toUpperCase()}}
             </th>
          </template>
          <template v-slot:[`header.examen`]="{ header }">
             <th  class="red darken-4 text-center subtitle-1 font-weight-bold white--text " >
                        {{header.text.toUpperCase()}}
             </th>
          </template>
           <template v-slot:[`header.especialidad`]="{ header }">
             <th  class="red darken-4 text-center subtitle-1 font-weight-bold white--text " >
                        {{header.text.toUpperCase()}}
             </th>
          </template>
          <template v-slot:[`header.fechaAtencion`]="{ header }">
             <th  class="red darken-4 text-center subtitle-1 font-weight-bold white--text " >
                        {{header.text.toUpperCase()}}
             </th>
          </template>
           <template v-slot:[`header.acciones`]="{ header }">
             <th  class="red darken-4 text-center subtitle-1 font-weight-bold white--text " >
                        {{header.text.toUpperCase()}}
             </th>
          </template>
           <template v-slot:[`item.estadoResultado`]="{ item }">
             <v-btn @click="cambiarEstado(item.id,item.estadoResultado)"  icon outlined  small class="ma-1" :class="{'loading':loading_btn_estado}">        
               <v-icon  v-if="item.estadoResultado==0" class="btn_red">circle</v-icon>
               <v-icon  v-else class="btn_green">circle</v-icon>
             </v-btn>  
          </template>   
           <template v-slot:[`item.acciones`]="{ item }">
             <v-btn @click="mostrarDetalleExamen(item)" icon outlined small class="ma-1">        
                <v-icon>view_list</v-icon>
              </v-btn>            
              <v-btn  icon  outlined small class="ma-1"
                @click="editarExamen(item)">
                                    <v-icon>edit</v-icon> 
                                </v-btn>
                 <v-btn elevation="3" icon outlined small class="ma-1" 
                  @click="eliminarDataExamenes(item)">
                 <v-icon>delete</v-icon> 
              </v-btn>
          </template>   
          </v-data-table>
        <v-dialog v-model="modalExamImagen"  max-width="500" persistent>
      <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">
            {{ pacienteDetalleImagen }}
          </div>
        </v-list-item-content>
        <v-simple-table dark dense fixed-header >
          <template v-slot:default>
            <thead >
                <tr>
                <th>
                  Fech.Registro
                </th>
                 <th>
                  Fech.Nacimiento
                 </th>
                 
                  <th>
                    NroVoucher
                  </th>
                  <th>
                    Placa
                  </th>
                  <th>
                    Fallas
                  </th>
                  <th>
                    Med.Informante
                  </th>
                  <th>
                    Med.Seguimiento
                  </th>
                   <th>
                    Resultado 
                  </th>
                </tr>
            </thead>
              <tbody>
                  <tr   v-for="(item,index) in dataDetalleImagenes " :key="index">
                    <td class="text-center">{{ convertidorFecha(item.fechaRegistroExamen)}}</td>
                    <td class="text-center">{{ convertidorFecha(item.fechaNacimiento) }}</td>
                    <td :class="{ 'red':item.nroVoucher==null}" class=" text-center">{{  item.nroVoucher }}</td>
                    <td class="text-center">{{item.placa }}</td>
                    <td class="text-center">{{item.nroFallas }}</td>
                    <td class="text-center">{{item.informante }}</td>
                    <td class="text-center">{{item.seguimiento }}</td>
                    <td class="text-center" > 
                    {{ convertidorFecha(item.fechaEntregaResultado)}}
                    </td>
                  </tr>
              </tbody>
          </template>
        </v-simple-table>
        <v-list-item-content>
          <v-layout mx-2 :justify-center="true">
            <v-btn @click="cerrarModal" rounded dark>Cerrar</v-btn>
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>
</div>
</template>
<script>
import {mapState} from 'vuex'
export default {
    name:'TablaImagenes',
    data() {
        return {
                loading_btn_estado:false,
                 loading:true,
                 dataDetalleImagenes:{},
                 pacienteDetalleImagen:'',
                 modalExamImagen:false,
                search: '',
                dataHeaders:[
                { text:'*',align: 'start',   sortable: false, value: 'index', class:"headerStyleLeft red darken-4 text-center  font-weight-bold white--text " },
                { text: 'DNI', sortable: false, value: 'dni',class:" red darken-4 text-center  font-weight-bold white--text" },
                { align: 'center',  sortable: false, text: 'PACIENTE', value: 'paciente',class:" red darken-4 text-center  font-weight-bold white--text" },
                { text: 'IMPORTE', sortable: false, value: 'importe',class:" red darken-4   font-weight-bold white--text" },
                { text: 'EXAMEN',  sortable: false, value: 'examen',class:"red darken-4 text-center  font-weight-bold white--text" },
                { text: 'FECH.ATENCION',  sortable: false, value: 'fechaAtencion' ,class:"headerStyleRigtht red darken-4 text-center  font-weight-bold white--text" },
                { text: 'ESPEC.',  sortable: false, value: 'especialidad' ,class:"headerStyleRigtht red darken-4 text-center  font-weight-bold white--text" },
                { text: 'FILTRAR',  value: 'estadoResultado' ,class:"headerStyleRigtht red darken-4 text-center  font-weight-bold white--text" },          
               { text: '',  sortable: false, value: 'acciones' ,class:"headerStyleRigtht red darken-4 text-center  font-weight-bold white--text" },          
             ],
                dataBody: [],
                dataAuxiliar:[],
                desde:'',
                hasta:''
            } 
     },
     async mounted(){
         await this.inicializar();
         this.listarExamenesImagenes();   
     },
      computed:{
        ...mapState(['token']),
     },
    methods: {
      cambiarEstado(idExamen,estadoResultado){
         let data={
           idExamen,
           estadoResultado
         }
         this.loading_btn_estado=true;
          var headers={"Content-Type":"application/json",
                            token:this.token}
        this.axios.post('/cambiarEstadoImagen',data,{headers}).
        then(res=>{
     
           this.dataBody.map(e=>{
             if(e.id  ==idExamen){
               e.estadoResultado=res.data.estadoResultado
               return
             }
           })
              this.loading_btn_estado=false;
        })
        .catch(err=>{
      this.loading_btn_estado=false;
        })
      },
      scrollToTop() {
                window.scrollTo(0,0);
           },
      inicializar(){
        this.desde=this.obtenerFecha(); 
        this.hasta=this.obtenerFecha(); 
      },
      obtenerFecha(fecha){
        let hoy =null;
        if(!fecha){
           hoy = new Date();                 
        }else{
            hoy = new Date(fecha);
        }
         var aÃ±o=hoy.getFullYear()
                  var mes=(hoy.getMonth() + 1)
                      mes=(mes<10)?('0'+mes):mes;
                  var dia=hoy.getDate()
                      dia=(dia<10)?('0'+dia):dia;
              return aÃ±o+'-'+mes+'-'+dia;
        },
      convertidorFecha(str){
        if(str==null){
        return 'Sin Data'
       }else{
        const f = new Date(str);
          var aÃ±o=f.getFullYear()
          var mes=(f.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=f.getDate()
              dia=(dia<10)?('0'+dia):dia;
       return dia+'/'+mes+'/'+aÃ±o;
        }
       },
    visualizarImagen(){
        // var array = new Uint8Array(arrayBuffer);
        //          var blob = new Blob([array], { type: "image/png" });
        //          const blobUrl = URL.createObjectURL(blob);
        //          console.log(blobUrl);
    },
    mostrarDetalleExamen(item){
      this.dataDetalleImagenes={item};
      this.pacienteDetalleImagen=item.nombres+' '+item.apellidos;
      this.modalExamImagen=true;
  },
      editarExamen(e){
        this.$emit('moverPosicion');
        this.$emit('dataModificarImagen',e);
      },
      eliminarDataExamenes(e){},
            listarExamenesImagenes(){
             let data={desde:this.desde,hasta:this.hasta}
              var headers={"Content-Type":"application/json",
                            token:this.token}
              this.axios.post('/listarExamenesImagenes',data,{headers}).then(res=>{ 
                            let index=0;
                            res.data=res.data.filter(e=>{ 
                            index++;
                            e.index=index;
                            e.paciente=e.nombres+' '+e.apellidos;
                            e.fechaAtencion=this.convertidorFecha(e.fechaAtencion);
                            return e });
                            console.log(res.data)
                            this.dataBody=res.data; 
                            this.loading=false;
                    }).catch(err=>{console.log(err)})
                },
        ShowDialog(exam,name,id){
            this.dataDialog.examen=exam;
            this.dataDialog.nombre=name;
            this.dataDialog.id=id;
            this.dialog=true;
        },
        cerrarModal(){
          this.modalExamImagen=false;
           this.dataDetalleImagenes={};
           this.pacienteDetalleImagen=''; 
        },
        Eliminar(id){
            console.log('Eliminando..'+id);
             this.dialog=false;
        }
    },  
}
</script>
<style scoped>

  i.v-icon.v-icon.btn_red{
    color: red;
  }
  
  i.v-icon.v-icon.btn_green{
    color: green;
  }
</style>